.noPasswordMessage {
  height: 20px;
  width: 100px;

  background: url('./images/plz_enter_pw.png') no-repeat;
  background-size: contain;
}

.changeFailedMessage {
  height: 20px;
  width: 100px;

  background: url('./images/change_failed.png') no-repeat;
  background-size: contain;
}

.changeSucceedMessage {
  height: 20px;
  width: 100px;

  background: url('./images/changepw_succeed.png') no-repeat;
  background-size: contain;
}

.passwordNotMatchMessage {
  height: 25px;
  width: 135px;

  background: url('./images/pw_not_matched.png') no-repeat;
  background-size: contain;
}

.submitChangePassword {
  height: 48px;
  width: 265px;
  background: url('./images/Changepassword_btn.png') no-repeat;
  background-size: contain;
  border: none;
  cursor: pointer;

}

.password {
  height: 28px;
  width: 250px;
  font-size: 1.25em;
  margin-bottom: 45px;
}

.InputField {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;

  width: 255px;
  height: 185px;
  margin-top: 165px;

}

.ChangePassword {
  min-width: 550px;
  min-height: 600px;
  margin: auto;
  margin-top: 150px;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;

  background-image: url('./images/Changepassword_bg.png');
  background-size:contain;
}



